        window.location.href = constants.site_payment + `?plan=${this.choose_plan}&cycle=${this.choose_cycle}`;
        return;
        var targetWin = window.open(constants.site_payment + `?plan=${this.choose_plan}&cycle=${this.choose_cycle}`, 'Payment', 'toolbar=no, location=no, directories=no, status=no, menubar=no, scrollbars=no, resizable=no, copyhistory=no, width=' + w + ', height=' + h + ', top=' + top + ', left=' + left);
      }
    }
  },
  mounted() {
    const url = window.location.href;
    if (url.toString().endsWith('side-bar-option=true')) {
      this.sidebarToggle(true);
    } else {
      this.sidebarToggle(false);
    }
  }
